import {
  React,
  connect,
  Link,
  reduxForm,
  Panel,
  PanelHeader,
  PanelBody,
  ToastNotification,
  localStorageencryp,
} from "../../../../components/helpers/library";
import { getDataParams2 } from "../../../../config/axios";
import HeadKirimBarang from "./HeadKirimBarang";

class LaporanKirimBarang extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      export: false,
      LaporanDataBarang: [],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
    // this.print = this.print.bind(this);
  }

  handleSubmit(data) {
    this.setState({
      isLoading: true,
    });
    if (data.kode_toko === undefined) {
      ToastNotification("info", "Kode Toko Harus Dipilih");
      this.setState({
        isLoading: false,
      });
      return false;
    }
    let data_post = {
      kode_toko: data.kode_toko,
      tgl_from : data.tgl_awal,
      tgl_to: data.tgl_akhir
    };
    getDataParams2(`kirim-barang/reports`, data_post)
      .then((res) => {
        localStorageencryp("tgl_laporan", JSON.stringify(data_post));

        this.setState({
          isLoading: false,
          export: true,
          LaporanDataBarang: res.data,
        });
      })
      .catch((err) => {
        ToastNotification(
          "info",
          err?.response?.data?.message ||
            "Terjadi Kesalahan Saat Merequest Data"
        );
        this.setState({
          isLoading: false,
        });
      });
  }

  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Laporan Barang</Link>
          </li>
          <li className="breadcrumb-item active">Laporan Kirim Barang</li>
        </ol>
        <h1 className="page-header">Laporan Kirim Barang </h1>
        <Panel>
          <PanelHeader>Laporan Kirim Barang</PanelHeader>
          <br />
          {/* Laporan Kirim Barang */}
          <PanelBody>
            <HeadKirimBarang
              export={this.state.export}
              LaporanDataBarang={this.state.LaporanDataBarang}
              isLoading={this.state.isLoading}
              onSubmit={(data) => this.handleSubmit(data)}
            />
          </PanelBody>
          <br />
        </Panel>
      </div>
    );
  }
}

LaporanKirimBarang = reduxForm({
  form: "LaporanKirimBarang",
  enableReinitialize: true,
})(LaporanKirimBarang);
export default connect()(LaporanKirimBarang);
