import {
  React,
  connect,
  Link,
  reduxForm,
  Panel,
  PanelHeader,
  PanelBody,
  getDataNoTimeOut2,
  convertDate,
  ToastNotification,
  localStorageencryp,
} from "../../../../components/helpers/library";
import HeadSortirSepuh from "./HeadSortirSepuh";

class LaporanSortirSepuh extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      export: false,
      type: "GLOBAL",
      databarang: [],
    };
  }

  handleSubmit(data) {
    this.setState({
      isLoading: true,
    });
    let data_post = {
      tgl_awal: convertDate(data.tgl_awal),
      tgl_akhir: convertDate(data.tgl_akhir),
      type: data.type,
    };
    getDataNoTimeOut2(
      "sortir-sepuh/reports?type=" +
        data_post.type +
        "&tgl_to=" +
        data_post.tgl_akhir +
        "&tgl_from=" +
        data_post.tgl_awal
    )
      .then((res) => {
        if (res.data.length === 0) {
          ToastNotification("info", "Data Yang Anda Cari Tidak Ada");
          this.setState({
            export: false,
            isLoading: false,
            databarang: [],
          });
        } else {
          ToastNotification("success", "Laporan Kirim Sepuh Tersedia");
          localStorageencryp("tgl_laporan", JSON.stringify(data_post));
          this.setState({
            databarang: [],
          });
          let databarang = [];
          if (this.state.type === "GLOBAL") {
            res.data.forEach((element) => {
              databarang.push(element);
            });
          } else {
            res.data.forEach((element) => {
              element.detail_sortir.forEach((el) => {
                databarang.push(el);
              });
            });
          }
          this.setState({
            export: true,
            isLoading: false,
            databarang: databarang,
          });
        }
      })
      .catch((err) => {
        this.setState({
          export: false,
          isLoading: false,
          databarang: [],
        });
        ToastNotification("info", "Data Yang Anda Cari Tidak Ada");
      });
  }
  chnageType(e) {
    this.setState({
      export: false,
      type: e,
      databarang: [],
    });
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Laporan</Link>
          </li>
          <li className="breadcrumb-item active">Laporan Sortir Sepuh</li>
        </ol>
        <h1 className="page-header">Laporan Sortir Sepuh </h1>
        <Panel>
          <PanelHeader>Laporan Sortir Sepuh</PanelHeader>
          <PanelBody>
            <HeadSortirSepuh
              type={this.state.type}
              chnageType={(data) => this.chnageType(data)}
              export={this.state.export}
              isLoading={this.state.isLoading}
              databarang={this.state.databarang}
              onSubmit={(data) => this.handleSubmit(data)}
            />
          </PanelBody>
        </Panel>
      </div>
    );
  }
}

LaporanSortirSepuh = reduxForm({
  form: "LaporanSortirSepuh",
  enableReinitialize: true,
})(LaporanSortirSepuh);
export default connect(null)(LaporanSortirSepuh);
